import React, {useState} from 'react';
import GameBoardBack from "../GameBoard/GameBoardBack";
import GameBoardFront from "../GameBoard/GameBoardFront";
import ReactCardFlip from 'react-card-flip';
import DATA from "../data.json"


const GameMain = () => {
    const array = DATA
    const [isFlipped, setIsFlipped] = useState(false)
    const [string, setString] = useState("BẤM ĐỂ CHƠI NÀO")
    const [data, setData] = useState({
        number: 0,
        content: array[0].content,
        drink: array[0].drink,
        end: array[0].end
    })
    const clickOpen = () => {
        let index = array.length
        let x = Math.floor(Math.random() * index);
        let y = Math.floor(Math.random() * 50);
        console.log(x)
        setData({
            number: y,
            content: array[x].content,
            drink: array[x].drink,
            end: array[x].end
        })
        setIsFlipped(true)
        array.splice(x, 1)
        console.log(array)
        setString("BẠN CỦA LAN")
    }

    return (
        <div className={"game-board-back"}>
            <ReactCardFlip isFlipped={isFlipped} flipDirection="horizontal" flipSpeedBackToFront={2}
                           flipSpeedFrontToBack={2}>
                <div className={"game-board-front"} onClick={() => {
                    clickOpen()
                }}>
                    <GameBoardFront text={string}/>
                </div>
                <div className={"game-board-back"} onClick={() => setIsFlipped(false)}>
                    <GameBoardBack data={data}/>
                </div>
            </ReactCardFlip>
        </div>
    )
};

export default GameMain;