import React from 'react';
import "./GameBoardBack.css"
import ICON_SOJU from "../../image/icon_soju.png"

const GameBoardBack = ({data}) => {
    return (
        <div className={"game-board-back__row"}>
            <img className={"game-board-back__icon left-top"} src={ICON_SOJU} alt=""/>
            <img className={"game-board-back__icon right-top"} src={ICON_SOJU} alt=""/>
            <img className={"game-board-back__icon left-bottom"} src={ICON_SOJU} alt=""/>
            <img className={"game-board-back__icon right-bottom"} src={ICON_SOJU} alt=""/>
            <div className={"game-board-back__text"}>
                <img className={"game-board-back__image"}
                     src={require('../../imageChibi/naruto' + data.number + '.png').default} alt=""/>
                <h1 className={"game-board-back__content"}>
                    {data.content}
                </h1>
                <h1 className={"game-board-back__drink"}>
                    {data.drink}
                </h1>
                <h1 className={"game-board-back__end"}>
                    {data.end}
                </h1>
            </div>
        </div>
    );
};

export default GameBoardBack;